<template>
  <div>
    <h1>短信回复</h1>
    <a-divider />
    <a-table :columns="columns" :rowKey="record => record.id" :dataSource="replyData" :pagination="pagination" :loading="loading" @change="handleTableChange">
      <template slot="mobile" slot-scope="mobile">
        {{mobile.first}} {{mobile.last}}
      </template>
    </a-table>
  </div>
</template>

<script>
const axios = require('axios')
const service = axios.create({
  baseURL: process.env.API_HOST,
  timeout: 5000,
  withCredentials: true
})

const columns = [{
  title: 'Mobile',
  dataIndex: 'mobile',
  width: '20%'
}, {
  title: 'Text',
  dataIndex: 'text'
}, {
  title: 'Time',
  dataIndex: 'time',
  sorter: true,
  width: '20%'
}]

export default {
  mounted () {
    this.fetch()
  },
  data () {
    return {
      replyData: [],
      pagination: {},
      loading: false,
      columns
    }
  },
  methods: {
    handleTableChange (pagination, filters, sorter) {
      console.log(sorter)
      if (sorter !== {}) {
        this.replyData = this.replyData.reverse()
      }
      const pager = { ...this.pagination }
      pager.current = pagination.current
      this.pagination = pager
    },
    fetch (params = {}) {
      console.log('params:', params)
      this.loading = true
      service
        .get('/message/sms/reply', { timeout: 4000 })
        .then(function (response) {
          this.replyData = response.data.data.reverse()
        }.bind(this))
        .catch(function (error) {
          if (error.response.data !== null) {
            this.$message.error(error.response.data.message)
          }
        }.bind(this))
        .then(function () {
          this.loading = false
        }.bind(this)
        )
    }
  }
}
</script>

<style>
</style>



